// @ts-nocheck
/**
 * 
 *
 *  Cytoscape 
 */
import { cleanRelationText, getRelationColor, shouldUseDashedLine } from './relationshipColors';
// ============================================================
// 
// ============================================================
/**  */
const SEGMENT_SEPARATORS = /[;|\n]/;
/**  */
const ITEM_SEPARATORS = /[,/&]/;
/**  */
const COLON_PATTERN = /[:]/;
// ============================================================
// 
// ============================================================
/**
 * 
 */
function getCellValue(row, colIndex) {
    const cell = row.cells.find(c => c.colIndex === colIndex);
    return cell ? String(cell.value ?? '').trim() : '';
}
/**
 * 
 * @param name 
 * @param aliasMap 
 * @returns 
 */
function resolveAlias(name, aliasMap) {
    if (!aliasMap || !name)
        return name;
    return aliasMap.get(name) || name;
}
/**
 * 
 */
function findColumnIndex(headers, possibleNames) {
    const lowerHeaders = headers.map(h => h.toLowerCase());
    for (const name of possibleNames) {
        const idx = lowerHeaders.findIndex(h => h.includes(name.toLowerCase()));
        if (idx !== -1)
            return idx;
    }
    return -1;
}
/**
 * /
 */
export function isCharacterTable(tableName, tableId) {
    const name = tableName.toLowerCase();
    const id = tableId.toLowerCase();
    const keywords = ['', '', 'npc', 'character', '', 'protagonist', 'player'];
    return keywords.some(kw => name.includes(kw) || id.includes(kw));
}
/**
 * 
 */
export function isRelationshipTable(tableName, tableId) {
    const name = tableName.toLowerCase();
    const id = tableId.toLowerCase();
    const keywords = ['', 'relationship', 'relation'];
    return keywords.some(kw => name.includes(kw) || id.includes(kw));
}
// ============================================================
//  - 
// ============================================================
/**
 * 
 */
export function isEnglishName(name) {
    return /^[A-Za-z\s'-]+$/.test(name) && name.includes(' ');
}
/**
 * 
 * - : 
 * - : 
 */
export function getDefaultLabel(fullName) {
    if (isEnglishName(fullName)) {
        // 
        const firstName = fullName.split(' ')[0];
        return firstName.length > 6 ? firstName.slice(0, 6) : firstName;
    }
    // 
    return fullName.length > 0 ? fullName[fullName.length - 1] : fullName;
}
// ============================================================
// 
// ============================================================
/**
 *  Cytoscape 
 *
 * @param relationshipTable 
 * @param characterTables 
 * @param factionTable 
 * @param aliasMap 
 * @param customLegendItems 
 * @returns 
 */
export function parseRelationshipTable(relationshipTable, characterTables = [], factionTable, aliasMap, customLegendItems) {
    const warnings = [];
    const nodes = [];
    const edges = [];
    const nodeIds = new Set();
    // 
    if (!relationshipTable || relationshipTable.rows.length === 0) {
        return { elements: [], nodeCount: 0, edgeCount: 0, warnings: [''] };
    }
    const headers = relationshipTable.headers;
    // 
    const sourceIdx = findColumnIndex(headers, ['source', '', '', '', 'from']);
    const targetIdx = findColumnIndex(headers, ['target', '', '', '', 'to']);
    const relationIdx = findColumnIndex(headers, ['', 'relation', '']);
    const noteIdx = findColumnIndex(headers, ['', 'note', '', 'remark']);
    // 
    if (sourceIdx === -1 || targetIdx === -1) {
        return {
            elements: [],
            nodeCount: 0,
            edgeCount: 0,
            warnings: [' source/target '],
        };
    }
    // 
    const protagonistNames = new Set();
    const characterNames = new Set();
    const factionNames = new Set();
    // 
    characterTables.forEach(table => {
        const tableName = table.name.toLowerCase();
        const isProtagonist = tableName.includes('') || tableName.includes('protagonist');
        const nameIdx = findColumnIndex(table.headers, ['', 'name', '', '']);
        if (nameIdx !== -1) {
            table.rows.forEach(row => {
                const name = getCellValue(row, nameIdx);
                if (name) {
                    if (isProtagonist) {
                        protagonistNames.add(name);
                    }
                    else {
                        characterNames.add(name);
                    }
                }
            });
        }
    });
    // 
    if (factionTable) {
        const nameIdx = findColumnIndex(factionTable.headers, ['', 'name', '']);
        if (nameIdx !== -1) {
            factionTable.rows.forEach(row => {
                const name = getCellValue(row, nameIdx);
                if (name)
                    factionNames.add(name);
            });
        }
    }
    /**
     * 
     */
    function getNodeType(name) {
        if (protagonistNames.has(name))
            return 'protagonist';
        if (characterNames.has(name))
            return 'character';
        if (factionNames.has(name))
            return 'faction';
        return 'unknown';
    }
    /**
     * 
     * - : 
     * - : 
     */
    function addNode(name) {
        if (!name || nodeIds.has(name))
            return;
        nodeIds.add(name);
        const nodeType = getNodeType(name);
        // 
        const defaultLabel = getDefaultLabel(name);
        nodes.push({
            data: {
                id: name,
                label: defaultLabel,
                type: nodeType,
            },
            classes: nodeType,
        });
    }
    // 
    relationshipTable.rows.forEach((row, rowIndex) => {
        const rawSource = getCellValue(row, sourceIdx);
        const rawTarget = getCellValue(row, targetIdx);
        const relation = relationIdx !== -1 ? getCellValue(row, relationIdx) : '';
        const note = noteIdx !== -1 ? getCellValue(row, noteIdx) : '';
        // 
        if (!rawSource || !rawTarget) {
            if (rawSource || rawTarget) {
                warnings.push(` ${rowIndex + 1} source: "${rawSource}", target: "${rawTarget}"`);
            }
            return;
        }
        // 
        const source = resolveAlias(rawSource, aliasMap);
        const target = resolveAlias(rawTarget, aliasMap);
        // 
        addNode(source);
        addNode(target);
        // 
        const colorResult = getRelationColor(relation, customLegendItems);
        // 
        edges.push({
            data: {
                id: `edge-${rowIndex}`,
                source,
                target,
                label: relation || '',
                note,
                color: colorResult.color,
                lineStyle: shouldUseDashedLine(note) ? 'dashed' : 'solid',
                level: colorResult.level,
            },
        });
    });
    return {
        elements: [...nodes, ...edges],
        nodeCount: nodes.length,
        edgeCount: edges.length,
        warnings,
    };
}
/**
 * 
 */
export function findRelationshipTable(tables) {
    return tables.find(t => isRelationshipTable(t.name, t.id)) || null;
}
/**
 * 
 */
export function findFactionTable(tables) {
    const keywords = ['', 'faction', '', 'organization', ''];
    return (tables.find(t => {
        const name = t.name.toLowerCase();
        const id = t.id.toLowerCase();
        return keywords.some(kw => name.includes(kw) || id.includes(kw));
    }) || null);
}
/**
 * 
 */
export function findCharacterTables(tables) {
    return tables.filter(t => isCharacterTable(t.name, t.id) && !isRelationshipTable(t.name, t.id));
}
