            uiStore.closeHistoryDialog();
        }
        /**  TableRow  cells  */
        function tableRowToCells(row) {
            const cells = {};
            row.cells.forEach((cell, index) => {
                cells[index] = String(cell.value);
            });
            return cells;
        }
