        /**
        /**
         * 
         */
        function handleDivinationConfirm(action = 'reveal') {
            const result = uiStore.divinationOverlay.result;
            if (result) {
                confirmDivination(result, action);
            }
            else {
                console.error('[ACU] Confirm divination failed: No result found');
            }
        }
        /**
         * 
         * @param prompt 
         */
        function handlePromptEditorSave(prompt) {
            setHiddenPrompt(prompt);
            setupSendIntercept();
        }
        /**
         * 
         * @param prompt 
         */
        function appendPromptToInput(prompt) {
            const wrapped = `<>\n${prompt}\n</>`;
            appendToInput(wrapped, '\n');
            toast.success('');
        }
        // ============================================================
        //  - 
