        /**  */
        function handleSettingsSave() {
            console.info('[ACU] ');
        }
        /**  -  */
        function handleManualUpdate() {
            uiStore.openManualUpdateDialog();
        }
        /**  */
        function handleOpenNative() {
            try {
                const w = window.parent || window;
                const api = w.AutoCardUpdaterAPI || window.AutoCardUpdaterAPI;
                if (api && typeof api.openVisualizer === 'function') {
                    api.openVisualizer();
                    console.info('[ACU] ');
                }
                else {
                    console.warn('[ACU] AutoCardUpdaterAPI.openVisualizer ');
                }
            }
            catch (error) {
                console.error('[ACU] :', error);
            }
        }
        /**  */
        async function handleSaveToFloor(floorIndex) {
            try {
                const success = await saveToDatabase(null, false, false, floorIndex);
                if (success) {
                    console.info(`[ACU]  ${floorIndex} `);
                    // 
                    await loadData();
                }
            }
            catch (error) {
                console.error('[ACU] :', error);
            }
        }
        /**  */
        async function handlePurgeRange(startFloor, endFloor) {
            try {
                await executePurgeFloorRange(startFloor, endFloor);
                console.info(`[ACU]  ${startFloor}  ${endFloor} `);
                // 
                await loadData();
            }
            catch (error) {
                console.error('[ACU] :', error);
            }
        }
        /**  */
        async function handleAdvancedPurgeConfirm(result) {
            console.info(`[ACU]  ${result.changedCount} :`, result.tables);
            // 
            await loadData();
        }
