                    const winConfig = winConfigRaw ? JSON.parse(winConfigRaw) : null;
                    // 
                    if (winConfig?.isCentered === true) {
                        isCenteredMode = true;
                        //  activeTab  null
                        uiStore.setActiveTab(null);
                        // 
                        uiStore.isCollapsed = false;
                        console.info('[ACU]  Tab');
                    }
                }
                catch (e) {
