                // 
                if (!uiStore.isPanelVisible || uiStore.isPinned) {
                    return;
                }
                const target = event.target;
                //  ACU 
                if (isInsideACU(target)) {
                    return;
                }
                // 
                uiStore.closePanel();
                console.info('[ACU] ');
            };
            // 
            parentDoc.addEventListener('click', handleClickOutside);
            // 
            onUnmounted(() => {
                parentDoc.removeEventListener('click', handleClickOutside);
            });
        });
