// @ts-nocheck
/**
 *  IndexedDB 
 *  Base64 
 */

const DB_NAME = 'acu_background_db';
const DB_VERSION = 1;
const STORE_NAME = 'backgrounds';

/**  Key */
export const GLOBAL_THEME_BG_KEY = 'global_theme_background';

let dbInstance: IDBDatabase | null = null;

/**
 * 
 */
export interface BackgroundRecord {
  /**  */
  chatId: string;
  /**  */
  imageData: Blob;
  /**  MIME  */
  mimeType: string;
  /**  */
  timestamp: number;
}

/**
 * 
 */
async function initDB(): Promise<IDBDatabase> {
  if (dbInstance) return dbInstance;

  return new Promise((resolve, reject) => {
    const request = indexedDB.open(DB_NAME, DB_VERSION);

    request.onerror = () => {
      console.error('[ACU Background] :', request.error);
      reject(request.error);
    };

    request.onsuccess = () => {
      dbInstance = request.result;
      console.info('[ACU Background] ');
      resolve(dbInstance);
    };

    request.onupgradeneeded = event => {
      const db = (event.target as IDBOpenDBRequest).result;

      if (!db.objectStoreNames.contains(STORE_NAME)) {
        db.createObjectStore(STORE_NAME, { keyPath: 'chatId' });
        console.info('[ACU Background] ');
      }
    };
  });
}

/**
 * 
 * @param chatId  ID
 * @param imageData  Blob 
 * @param mimeType 
 */
export async function saveBackground(chatId: string, imageData: Blob, mimeType: string = 'image/png'): Promise<void> {
  const db = await initDB();
  return new Promise((resolve, reject) => {
    const tx = db.transaction(STORE_NAME, 'readwrite');
    const store = tx.objectStore(STORE_NAME);

    const record: BackgroundRecord = {
      chatId,
      imageData,
      mimeType,
      timestamp: Date.now(),
    };

    const request = store.put(record);

    request.onsuccess = () => {
      console.info('[ACU Background] , chatId:', chatId);
      resolve();
    };

    request.onerror = () => {
      console.error('[ACU Background] :', request.error);
      reject(request.error);
    };
  });
}

/**
 * 
 * @param chatId  ID
 * @returns blob URL  null
 */
export async function loadBackground(chatId: string): Promise<string | null> {
  const db = await initDB();
  return new Promise((resolve, reject) => {
    const tx = db.transaction(STORE_NAME, 'readonly');
    const store = tx.objectStore(STORE_NAME);
    const request = store.get(chatId);

    request.onsuccess = () => {
      const record = request.result as BackgroundRecord | undefined;
      if (record?.imageData) {
        const blobUrl = URL.createObjectURL(record.imageData);
        console.info('[ACU Background] , chatId:', chatId);
        resolve(blobUrl);
      } else {
        resolve(null);
      }
    };

    request.onerror = () => {
      console.error('[ACU Background] :', request.error);
      reject(request.error);
    };
  });
}

/**
 * 
 * @param chatId  ID
 */
export async function deleteBackground(chatId: string): Promise<void> {
  const db = await initDB();
  return new Promise((resolve, reject) => {
    const tx = db.transaction(STORE_NAME, 'readwrite');
    const store = tx.objectStore(STORE_NAME);
    const request = store.delete(chatId);

    request.onsuccess = () => {
      console.info('[ACU Background] , chatId:', chatId);
      resolve();
    };

    request.onerror = () => {
      console.error('[ACU Background] :', request.error);
      reject(request.error);
    };
  });
}

/**
 * 
 * @param chatId  ID
 */
export async function hasBackground(chatId: string): Promise<boolean> {
  const db = await initDB();
  return new Promise((resolve, reject) => {
    const tx = db.transaction(STORE_NAME, 'readonly');
    const store = tx.objectStore(STORE_NAME);
    const request = store.count(IDBKeyRange.only(chatId));

    request.onsuccess = () => {
      resolve(request.result > 0);
    };

    request.onerror = () => {
      console.error('[ACU Background] :', request.error);
      reject(request.error);
    };
  });
}

/**
 *  File/Blob 
 * @param file 
 */
export async function fileToBlob(file: File): Promise<{ blob: Blob; mimeType: string }> {
  return {
    blob: file,
    mimeType: file.type || 'image/png',
  };
}

/**
 *  URL data URL Blob
 * @param url  URL
 */
export async function urlToBlob(url: string): Promise<{ blob: Blob; mimeType: string }> {
  //  data URL
  if (url.startsWith('data:')) {
    const response = await fetch(url);
    const blob = await response.blob();
    return { blob, mimeType: blob.type };
  }

  // 
  try {
    const response = await fetch(url, { mode: 'cors' });
    if (!response.ok) {
      throw new Error(`HTTP ${response.status}`);
    }
    const blob = await response.blob();
    return { blob, mimeType: blob.type };
  } catch {
    throw new Error('');
  }
}

/**
 *  blob URL 
 * @param blobUrl blob URL
 */
export function revokeBlobUrl(blobUrl: string): void {
  if (blobUrl.startsWith('blob:')) {
    URL.revokeObjectURL(blobUrl);
  }
}
