    /**
     * 
     *  visibleTabs 
     * @param allTableIds  ID 
     * @returns  ID 
     */
    function syncNewTablesToVisibleTabs(allTableIds) {
        //  visibleTabs 
        if (visibleTabs.value.length === 0) {
            return [];
        }
        //  visibleTabs 
        const newTabIds = allTableIds.filter(id => !visibleTabs.value.includes(id));
        // 
        if (newTabIds.length > 0) {
            visibleTabs.value = [...visibleTabs.value, ...newTabIds];
            console.info(`[ACU]  ${newTabIds.length} :`, newTabIds);
        }
        return newTabIds;
    }
    // ============================================================
